.mainContainer{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
}

.eachCart{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    background-color: #ebe9e9;
}
.eachCart h2 , .eachCart p{
    text-align: start;
    left: 15px;
    color: white;
    position: absolute;
}
.eachCart h2 {
    top:10px;
    z-index: 1;
}
.eachCart p{
    width: 550px;
    top: 80px;
}
.eachCart img{
    height: 25vw;
    object-fit: cover;
    width: 100%;
    filter: grayscale(0);
    transition: 0.5s ease-in-out;
    overflow: hidden;
  
}
.eachCart img:hover{
    filter: grayscale(0.8);
  transition: 0.5s ease-in-out;

}